@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@100;300;400;500;700&display=swap');

@font-face {
    font-family: 'Roboto', sans-serif;
    src: url('https://fonts.googleapis.com/css2?family=Roboto:wght@100;300;400;500;700&display=swap')
  }
body{
    width: 500px;
    
    font-family: 'Roboto', sans-serif;
    text-transform: uppercase;
    font-size: 500;
    font-weight: 500;
    margin: auto;
}
.preLoader{
    position: fixed;
width: 100%;
height: 100%;
background-color: #fff;
left: 0;
top: 0;

}
.loader{
width: 75px;
height: 75px;
border: 10px solid black;
border-radius: 50%;
border-top-color:rgb(238, 237, 237);
position: absolute;
left: 50%;
top:50%;
transform: translate(-50%, -50%);
animation: 2s spin infinite linear;
}
@keyframes spin{
    from{
        transform:  translate(-50%, -50%) rotate(0deg) ;
    }
    to{
        transform:  translate(-50%, -50%)  rotate(360deg) ;
    }
}

.wrapper{
    margin: 25% 20%;
}
.wrapper p{
    text-align: center;
    font-weight: 600;
} 
.weather_info{
    
    display: grid;
    grid-template: auto auto auto auto/1fr;
    row-gap: 10px;
    transition: .5s;
}
.weather_info img{
    width: 100%;
    padding: 15px 0;
}
.top_info{
    text-align: center;
}
.top_info p:nth-child(1){
    font-size: 2.1em;
    padding: 5px;
    font-weight: 500;
}
.top_info p:nth-child(2){
    font-size: 1.2em;
    padding: 5px;
    font-weight: 400;
}
.bot_info{
    display: grid;
    grid-template: 1fr/1fr 1fr 1fr  ;
    text-transform: lowercase;

}
.bot_info p{
    font-size: 1.0em;
    text-align: center;
    font-weight: 500;
}
.bot_info p:nth-child(2){
    font-size: 2.9em;
    font-weight: 600;
}
.message{
    display: grid;
    grid-template: 1fr/1fr;
    justify-items: center;
}
.message p{
    font-size: .9em;
    text-align: center;
    font-weight: 500;
    line-height: 1.3em;
}
.wrapper form input{
    border: none;
    height: 40px;
}
.wrapper form input:{
    background-color: #fff;
}
.wrapper form button{
    border: none;
    height: 40px;
    background-color: #000;
    color: #fff;
}
.wrapper form {
    display: grid;
    row-gap: 5px;
}
@media all and (max-width:720px){
    .bot_info p:nth-child(2){
        font-size: 1.9em;
        font-weight: 600;
    }
    body{
        width: auto;
        margin: 0;
    }
    .wrapper{
        margin: 17% 20%;
    }
}